<template>
	<div class="">
      <div class="img-cont">
        <i><img :src="imgurl" alt=""></i>
      </div>

    </div>
</template>

<script>
export default {
  name: 'LookCert',
  data () {
      return {
        curCertId:'',
        imgurl:''
      }
  },
  created:function(){

    this.getParams();
  },
  methods: {
    getParams: function () {
      var self = this;
      self.curCertId = self.$route.query.curcertid;
      self.getImg();
    },
    getImg(){
      let self = this;
      self.$axios.get(self.baseUrl+'/api/v1/weixin/e-certs/'+self.curCertId + '/'+localStorage.openid).then(res =>{
        if(res.data.success){
          self.imgurl = res.data.data.image_url;
        }else{
          self.$toast.center(res.data.msg);
        }

      })


    },


   },
   computed: {

   }




}
</script>

<style scoped>
.img-cont{
  width:100%;
  height:100%;
  position: absolute;
  background-color:rgba(0,0,0,0.8);
}
.img-cont i{
  display: inline-block;
  margin:50px auto;
    width:600px;
    /*height:86px;*/
    vertical-align: middle;
}
img{
    width:100%;
    height:100%;
}
</style>



